
import Alert from 'vux/src/components/alert/index.vue'
import dateFormat from 'vux/src/tools/date/format.js'
import Radio from 'vux/src/components/radio/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import Checker from 'vux/src/components/checker/checker.vue'
import CheckerItem from 'vux/src/components/checker/checker-item.vue'
import Divider from 'vux/src/components/divider/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Popup from 'vux/src/components/popup/index.vue'

import Qs from 'qs'
export default {
components: { Alert, dateFormat, Radio, XButton, XInput, XHeader, Checker, CheckerItem, Divider, Group, Cell, Popup },
data () {
return {
opportunityId: '',
payId: '',
payMoney: 0,
payTime: '',
btn: true,
tip: false
}
},
watch: {
},
beforeCreate () {
},
created () {
this.opportunityId = this.$route.query.opportunityId
console.log('opportunityId:' + this.opportunityId)
var send = Qs.stringify({payId: this.$route.query.payId})
this.$http.post('/order/isPaySuccess', send, {headers: {'Content-Type': 'application/x-www-form-urlencoded'}}).then(response => {
if (response.data.code === 0) {
console.log(response.data.data)
this.payMoney = Number(response.data.data.total_fee / 100)
this.payTime = response.data.data.time_end
this.payTime = this.dataformat(this.payTime, 'YYYY-MM-DD HH:mm:ss')
} else {
this.$vux.alert.show({
title: '提示',
content: '此订单没有支付成功'
})
}
})
if (!this.isWeiXin()) {
this.btn = false
this.tip = true
}
},
methods: {
isWeiXin () { // 判断是否微信浏览器
var ua = window.navigator.userAgent.toLowerCase()
if (ua.match(/MicroMessenger/i) + '' === 'micromessenger') { // 并不是全=

return true
} else {

return false
}
},
getPayMoney () {
},
dataformat (val) { // 20091225091010
var arr = []
arr[0] = val[0] + val[1] + val[2] + val[3]
arr[1] = val[4] + val[5]
arr[2] = val[6] + val[7]
arr[3] = val[8] + val[9]
arr[4] = val[10] + val[11]
arr[5] = val[12] + val[13]
return arr[0] + '-' + arr[1] + '-' + arr[2] + ' ' + arr[3] + ':' + arr[4] + ':' + arr[5]
},
checkOrder () {
this.to()
},
to () {

this.$router.push({path: '/myNeed'})
}
/* guid () {
return 'xxxxxxxx-xxxx-4xxx-yxxx-xxxxxxxxxxxx'.replace(/[xy]/g, function(c) {
var r = Math.random()*16|0, v = c == 'x' ? r : (r&0x3|0x8)
return v.toString(16)
})
} */
}
}



