
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import Previewer from 'vux/src/components/previewer/index.vue'
import Radio from 'vux/src/components/radio/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Popup from 'vux/src/components/popup/index.vue'

import Qs from 'qs'
export default {
directives: {
TransferDom
},
components: { Previewer, Radio, XButton, XInput, Group, Cell, Popup },
data () {
return {
hint: false,
items: [],
list: [{
src: ''
}],
options: {
getThumbBoundsFn (index) {

let thumbnail = document.querySelectorAll('.previewer-demo-img')[index]

let pageYScroll = window.pageYOffset || document.documentElement.scrollTop


let rect = thumbnail.getBoundingClientRect()

return {x: rect.left, y: rect.top + pageYScroll, w: rect.width}


}
}
}
},
created () {
var send = Qs.stringify({optyId: this.$route.query.opportunityId, userId: this.get('userId')})
var self = this
this.$http.post('/attach/show', send, {headers: {'Content-Type': 'application/x-www-form-urlencoded'}}).then(response => {

self.items = response.data.data
console.log(self.items)
if (response.data.data.length === 0) {
self.hint = true
}

})
},
methods: {
show (arr, index) {
var self = this
this.list = arr
setTimeout(function () {
self.$refs.previewer.show(index)
}, 100)
},
get (name) {
var v = window.document.cookie.match('(^|;) ?' + name + '=([^;]*)(;|$)')
return v ? v[2] : null
}
}
}




