

import Vue from 'vue'

import FastClick from 'fastclick'
import VueRouter from 'vue-router'
import App from './App'
import PersonalInfo from './components/personalInfo'
import MyNeed from './components/myNeed'
import ReserveMeasure from './components/reserveMeasure'
import NeedDetail from './components/needDetail'
import NeedModify from './components/needModify'
import PayOrder from './components/payOrder'
import PayResult from './components/payResult'
import zhifubao from './components/zhifubao'
import MyOrder from './components/myOrder'
import OrderDetail from './components/OrderDetail'
import Evaluate from './components/evaluate'
import MyShop from './components/myShop'
import Picture from './components/picture'
import Introduce from './components/introduce'
import ShopDetail from './components/shopDetail'
import SaleFeedback from './components/saleFeedback'
import axios from 'axios'

import LoadingPlugin from 'vux/src/plugins/loading/index.js'
import AlertPlugin from 'vux/src/plugins/alert/index.js'
import ConfirmPlugin from 'vux/src/plugins/confirm/index.js'





Vue.use(LoadingPlugin)
Vue.use(ConfirmPlugin)
Vue.use(AlertPlugin)
Vue.use(VueRouter)

/* var axiosInstance = axios.create({
transformRequest: [function (data) {
data = Qs.stringify(data)
return data
}],
headers: {'Content-Type': 'application/x-www-form-urlencoded'}
})
Vue.use(VueAxios, axiosInstance) */

Vue.prototype.$http = axios




if (process.env.NODE_ENV === 'development') {

axios.defaults.baseURL = 'http://milinkInfo.com/nobee_weixin_ipad_api'

} else {

axios.defaults.baseURL = 'http://milinkInfo.com/nobee_weixin_ipad_api'

}
/* Vue.http.defaults.transformRequest = [function (data) {
return querystring.stringify(data)
}] */


Vue.directive('title', {
inserted: function (el, binding) {
document.title = el.innerText
el.remove()
}
})
Vue.component('introduce', {
template: '<template><div v-html="introduce"></div></template>'
})
const routes = [{
path: '/',
component: PersonalInfo
},
{
path: '/myNeed',
component: MyNeed
},
{
path: '/reserveMeasure',
component: ReserveMeasure
},
{
path: '/needDetail',
component: NeedDetail
},
{
path: '/needModify',
component: NeedModify
},
{
path: '/gzh/or/payOrder',
component: PayOrder
},
{
path: '/payResult',
component: PayResult
},
{
path: '/zhifubao',
component: zhifubao
},
{
path: '/evaluate',
component: Evaluate
},
{
path: '/myOrder',
component: MyOrder
},
{
path: '/orderDetail',
component: OrderDetail
},
{
path: '/myShop',
component: MyShop
},
{
path: '/introduce',
component: Introduce
},
{
path: '/picture',
component: Picture
},
{
path: '/shopDetail',
component: ShopDetail
},
{
path: '/saleFeedback',
component: SaleFeedback
}]

const router = new VueRouter({

routes
})

FastClick.attach(document.body)

Vue.config.productionTip = false

/* eslint-disable no-new */
new Vue({
router,
render: h => h(App)
}).$mount('#app-box')



